var common = {
  render: function (str) {
    var body = document.body;
    body.innerHTML = str + body.innerHTML;
  },

  isAllLoaded(scope, cb) {
      var t_img; // 
      var isLoad = true; // 

      // 
      return isImgLoad(cb);

      // 
      function isImgLoad(callback) {
          // 
          //console.log(scope)
          //console.log(typeof scope)
          var sco = document.getElementById(scope);
          //console.log(sco)
          var tar = sco.getElementsByTagName("img");
          //console.log(tar)
          //
          // for(var key in tar){
          //   //for in length 
          //   console.log(tar[key])
          // }
          //
          // function realArray(c) {
          //   try {
          //     return Array.prototype.slice.call(c);
          //   } catch (e) {
          //     var ret = [], i = 0, len = c.length;
          //     for (; i < len; i++) {
          //       ret[i] = (c[i]);
          //     }
          //     return ret;
          //   }
          // }
          // for(var key in realArray(tar)){
          //   //for in length 
          //   console.log(realArray(tar)[key])
          // }

          var getStyle = (function(){
             if(window.VBArray){
               return function(ele, attr){
                 return ele.currentStyle[attr];
               }
             }else{
               return function(ele, attr){
                 return getComputedStyle(ele,null)[attr];
               }
             }
          })();

          //
          for(var i=0; i<tar.length; i++){
            //console.log(tar[i])
            // 0isLoadfalseeach
            if ( getStyle(tar[i], "height") === 0) {
                isLoad = false;
                return false;
            }
          }

          // true0
          if (isLoad) {
              clearTimeout(t_img); // 
              // 
              callback();
          // false
          } else {
              isLoad = true;
              t_img = setTimeout(function() {
                  isImgLoad(callback); // 
              }, 500); // 500
          }
      }
  },

  scroll: function (that,n) {
    var myScroll = new IScroll('#iscrollWrapper', {
        probeType: 3,//probeType3  
        mouseWheel: true
    });

    var scrollHeight = 35;
    //
    //$('.head.hide').removeClass('hide');
    //$('.foot.hide').removeClass('hide');
    that.isTrue = false;
    // scrollByxytimeeasing
    myScroll.scrollBy(0, -scrollHeight);

    //
    //img 
    //  var head = $('.head img'),
    //      topImgHasClass = head.hasClass('up');
    //  var foot = $('.foot img'),
    //      bottomImgHasClass = head.hasClass('down');
    //
    myScroll.on('scroll', function () {
        var y = this.y,//
            //maxScrollY: myScroll.x/y    -2 - -1 = -1; -1*(2-1) = -1;;;;   maxY:
            maxY = this.maxScrollY - y;
        // if (y >= 0) {
        //     !topImgHasClass && head.addClass('up');
        //     return '';
        // }
        // if (maxY >= 0) {
        //     !bottomImgHasClass && foot.addClass('down');
        //     return '';
        // }

        //
        if (y >= 0) {
            that.headFalse = true;
            return '';
        }
        //
        if (maxY >= 0) {
            that.footFalse = true;
            return '';
        }
    });
    //
    var n=0;
    myScroll.on('scrollEnd', function () {
        //scrollHeightscrollHeight
        if (this.y >= -scrollHeight && this.y < 0) {
            myScroll.scrollTo(0, -scrollHeight);
            /*myScroll.scrollTo(0, -100);
            1000
            timeeasingms
            IScroll.utils.ease1
            myScroll.scrollTo(0, -100, 1000, IScroll.utils.ease.elastic);
            quadraticcircularbackbounceelastic*/

            //head.removeClass('up');
            that.headFalse = false;
        } else if (this.y >= 0) {
            //head.attr('src', '/images/ajax-loader.gif');
            that.src = '/static/images/ajax-loader.gif';
            //TODO ajax
            setTimeout(function () {
                myScroll.scrollTo(0, -scrollHeight);
                //head.removeClass('up');
                that.headFalse = false;
                //head.attr('src', '/images/arrow.png');
                that.src = '/static/images/arrow.png'
            }, 100);
        }
        //maxY:
        var maxY = this.maxScrollY - this.y;
        if (maxY > -scrollHeight && maxY < 0) {
            var self = this;
            //scrollHeight
            myScroll.scrollTo(0, self.maxScrollY + scrollHeight);
            //foot.removeClass('down')
            that.footFalse = false;

        } else if (maxY >= 0) {
            //foot.attr('src', '/images/ajax-loader.gif');
            that.src = '/static/images/ajax-loader.gif';
            //TODO ajax
            var self = this;

            // fetch('/api/iscroll')
            // .then(response => response.json())
            // .then(res => {
            //   that.isc = that.isc.concat(res);

            //   myScroll.refresh();

            //   //myScroll.scrollTo(0, self.y + scrollHeight);
            //   myScroll.scrollTo(0, self.maxScrollY - 60);

            //   //foot.removeClass('down');
            //   that.footFalse = false;
            //   //foot.attr('src', '/images/arrow.png');
            //   that.src = '/static/images/arrow.png'
            // })

            n++;
            //console.log(n)
            common.ajax("POST",'/api/iscroll','len=2&n='+n,function (res) {
              if (res) {
                that.isc = that.isc.concat(res);
              }
              
              myScroll.refresh();

              //myScroll.scrollTo(0, self.y + scrollHeight);
              myScroll.scrollTo(0, self.maxScrollY - 60);

              //foot.removeClass('down');
              that.footFalse = false;
              //foot.attr('src', '/images/arrow.png');
              that.src = '/static/images/arrow.png'
              //console.log(res)
              if (res == false) {
                //console.log(true)
                that.isTrue = true;
              }
            })


        }
    })
  },//scroll end
  packageSwiper(name){
		return new Swiper(name, {
			autoplay: 5000,//
            initialSlide :0,//
            direction : 'horizontal',//|vertical
            speed:300,//
            autoplayDisableOnInteraction : false,//
            autoplayStopOnLast : false,//trueslideloop
            grabCursor : true,//trueSwiper
            parallax : true,//banner
            hashnav:false,//swiperurlurlswiperslide
            hashnavWatchState:false,
            setWrapperSize :true,//Wrapperslidesflexbox
            virtualTranslate : false,//trueSwiper
            roundLengths : true,//trueslide()(border)
            autoHeight: true,//truewrappercontainerslide
            a11y: true,//
            prevSlideMessage: 'Previous slide',
            nextSlideMessage: 'Next slide',
            firstSlideMessage: 'This is the first slide',
            lastSlideMessage: 'This is the last slide',
            paginationBulletMessage:'Go to slide {{index}}',
            nextButton: '.swiper-button-next',//
            prevButton: '.swiper-button-prev',//
            pagination : '.swiper-pagination',//
            //pagination : '#swiper-pagination1',
            paginationHide :false,//true
            paginationType : 'bullets',//:bullets  fraction   progress  custom 
            paginationClickable :true,//trueSwiper
            //paginationElement : 'li',//HTML
        /* paginationBulletRender: function (swiper, index, className) {
    return '<span class="' + className + '">' + (index + 1) + '</span>';
    },*///fraction/progress/customAPI
                //uniqueNavElements :false,//trueswipercontainerfalsecontainer
                //scrollbar:'.swiper-scrollbar',//
                //scrollbarHide:false,//true
                //scrollbarDraggable : true ,//true
                //scrollbarSnapOnRelease : true ,//

            effect: 'cube',//slide"slide""fade""cube""coverflow"3d"flip"3d
            cube: {
            slideShadows: true,//slideShadowsslide true
            shadow: true,//shadow  true
            shadowOffset: 100,//shadowOffset 20px
            shadowScale: 0.6//shadowScale 0.94
            },
            //mousewheelControl : true,//
            lazyLoading : true,//true
            lazyLoadingInPrevNext : true,//slideslide
            lazyLoadingInPrevNextAmount : 2,//11slideslideslide
            loop : true,//
            freeMode : false
		})//Swiper end
		
    },//packageSwiper end
    ajax(type,url,data,callback){
  	  //ajax
  	  var xhr;
  		// console.log(xhr);
      var getxhr = (function () {
        if(window.XMLHttpRequest){
          return function () {
            xhr = new XMLHttpRequest();
          }
    		}else{
          return function () {
            xhr = new ActiveXObject("Microsoft.XMLHTTP");
          }
    		}
      })();
  		getxhr();
  		//
  		xhr.open(type,url,true);
      //
      if (type == 'POST') {
        xhr.setRequestHeader("Content-Type","application/x-www-form-urlencoded;charset=UTF-8");
      }
  		//
  		xhr.onreadystatechange=function(){
        if(xhr.readyState==4&&xhr.status==200){
          var obj = JSON.parse(xhr.responseText);
          //console.log(obj)
          callback(obj);
        }else{
          // console.log(xhr.readyState +" "+xhr.status);
        }
  		}
  		//
      if (type == 'POST') {
        var da = data;
      } else {
        var da = null;
      }
  		xhr.send(da);
  },//ajax end
  getCookie(cname){
    var name = cname + "=";
    var ca = document.cookie.split(';');
    for(var i=0; i<ca.length; i++) {
      var c = ca[i].trim();
      if (c.indexOf(name)==0) return c.substring(name.length,c.length);
    }
    return "";
  },// getcookie end
  // formform
  formToString (formObj) {
    var allStr = ''
    if (formObj) {
      var elementsObj = formObj.elements
      var obj
      if(elementsObj){
        for(var i=0; i<elementsObj.length; i++){
          obj=elementsObj[i]
          if(obj.name != undefined && obj.name != ""){
            allStr+="&"+obj.name+"="+encodeURIComponent(obj.value)
          }
        }
      }else{
        alert("elements!")
        return
      }
    }else{
      alert("form!")
      return
    }
    return allStr
  }//getform end
}//common end

module.exports = common;



