<template>
<div id="m-b-template">
  <div class="m-b-tem-main">
    <ul>
      <li><h2>vue</h2></li>
      <li>
        <h3>v-text</h3>
        <p>
          <textarea :style="s" :wrap="off">
            //
            &lt;span v-text="msg">&lt;/span>
            <!--  -->
            &lt;span>{{msg}}&lt;/span>
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-html</h3>
        <p>
          <textarea :style="s" :wrap="off">
            //HTML
            &lt;div v-html="html">&lt;/div>
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-show</h3>
        <p>
          <textarea :style="s" :wrap="off">
             display CSS 
            &lt;h1 v-show="ok">Hello! &lt;/h1>
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-if</h3>
        <p>
          <textarea :style="s" :wrap="off">
            v-showv-iffalse
            &lt;h1 v-if="ok">Yes &lt;/h1>
            &lt;h1 v-else>No &lt;/h1>
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-else-if</h3>
        <p>
          <textarea :style="s" :wrap="off">
             v-else ,v-else-if  v-if  v-else-if
            &lt;div v-if="type === 'A'">
              A
            &lt;/div>
            &lt;div v-else-if="type === 'B'">
              B
            &lt;/div>
            &lt;div v-else-if="type === 'C'">
              C
            &lt;/div>
            &lt;div v-else>
              Not A/B/C
            &lt;/div>
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-for</h3>
        <p>
          <textarea :style="s" :wrap="off">
            &lt;div v-for="item in items">
              &lt;ul>
                &lt;li>{{ "item." }}&lt;/li>
              &lt;/ul>
            &lt;/div>
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-on</h3>
        <p>
          <textarea :style="s" :wrap="off">
            ,  DOM 
            <!--  -->
            &lt;button v-on:click="doThat('hello', $event)">&lt;/button>
            <!--  -->
            &lt;button @click="doThis">&lt;/button>
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-bind</h3>
        <p>
          <textarea :style="s" :wrap="off">
            
            <!--  -->
            &lt;img v-bind:src="imageSrc">
            <!--  -->
            &lt;img :src="imageSrc">
            <!-- class  -->
            &lt;div :class="{ red: isRed }">&lt;/div>
            &lt;div :class="[classA, classB]">&lt;/div>
            &lt;div :class="[classA, { classB: isB, classC: isC }]">
            <!-- style  -->
            &lt;div :style="{ fontSize: size + 'px' }">&lt;/div>
            &lt;div :style="[styleObjectA, styleObjectB]">&lt;/div>
            <!-- prop . prop  my-component  -->
            &lt;my-component :prop="someThing">&lt;/my-component>
            prop 
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-model</h3>
        <p>
          <textarea :style="s" :wrap="off">
            
            &lt;input v-model="message" placeholder="edit me">
            &lt;p>Message is: {{ message }}&lt;/p>
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-pre</h3>
        <p>
          <textarea :style="s" :wrap="off">
            
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-cloak</h3>
        <p>
          <textarea :style="s" :wrap="off">
             CSS  [v-cloak] { display: none }  Mustache 
            [v-cloak] {
              display: none;
            }
            &lt;div v-cloak>
              {{ message }}
            &lt;/div>
          </textarea>
        </p>      
      </li>
      <li>
        <h3>v-once</h3>
        <p>
          <textarea :style="s" :wrap="off">
            ,/
            <!--  -->
            &lt;span v-once>This will never change: {{msg}}&lt;/span>
            <!--  -->
            &lt;div v-once>
              &lt;h1>comment&lt;/h1>
              &lt;p>{{msg}}&lt;/p>
            &lt;/div>
            <!--  -->
            &lt;my-component v-once :comment="msg">&lt;/my-component>
            <!-- v-for -->
            &lt;ul>
              &lt;li v-for="i in list" v-once>{{i}}&lt;/li>
            &lt;/ul>
          </textarea>
        </p>      
      </li>
    </ul>
  </div>
  <div class="m-b-tem-comment">
    <Comment v-bind:dataId="dataId" v-if="dataId" v-bind:commeRes="commeRes"></Comment>
  </div>

</div>
</template>


<script>
  import Comment from './comment'
  export default {
    props:['essayData','dataId','commeRes'],
    components: {
      Comment
    },
    data () {
      return {
        commeRes1: this.commeRes,
        s:{
          'resize': 'none',
          'overflow-x': 'scroll !important',
          'width': '100%',
          'height': '100%',
          'white-space':'pre',
          'border':'#000 solid 1px'
				}
      }
      
    },
    computed: {
      commeRes0: function () {
        return this.commeRes
      }
    },
    mounted () {

    },
    updated () {
      // console.log(this.commeRes1+"commeRes1")// data
      // console.log(this.commeRes0+"commeRes0")//computed 
      // console.log(this.commeRes+"commeRestemplate")//prop
    }
  }
</script>

<style scoped>
  .m-b-tem-main{
    width:100%;
  }
  .m-b-tem-main ul{
    padding: .1rem;
    padding-bottom: .7rem;
  }
  .m-b-tem-main ul li{
    margin: 0.05rem 0;
  }
  .m-b-tem-main ul li h2{
    font-size:.30rem;
    color:#000;
    font-weight: bold;
  }
  .m-b-tem-main ul li h3{
    font-size:.20rem;
    color:#000;
  }
  .m-b-tem-main ul li img{
    width:100%;
    height: 100%;
  }
  .m-b-tem-main ul li p{
    font-size: .16rem;
    color:#1E1E1E;
    margin: 0.1rem 0;
    height: 2rem;
  }

  @media all and (min-width:1025px){
      .m-b-tem-main ul{
        padding-bottom: .1rem;
      }
      .m-b-tem-main ul li{
        margin: 0.05rem 0;
      }
      .m-b-tem-main ul li h2{
        font-size:0.1rem;
        color:#000;
        font-weight: bold;
      }
      .m-b-tem-main ul li h3{
        font-size:0.06rem;
        color:#000;
      }
      .m-b-tem-main ul li img{
        width:50%;
        display: block;
      }
      .m-b-tem-main ul li p{
        font-size: 0.04rem;
        color:#1E1E1E;
        margin: 0.05rem 0;
        height: 0.6rem
      }
  }
</style>


